import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
    import Music from './Music'

const Title = () => {
    return (
        <>
            <Row className="vh-10">
                <Col className="w-100 text-center text-white wow fadeInLeft" data-wow-delay="0.2s" data-wow-duration="1.2s" style={{  cssText: 'margin-top: 1vh !important; font-family: WriteSong; font-size: 2rem' }}>
                    <b>MisQuince</b>
                </Col>                
            </Row>                    
            <Row className="vh-10">
                <Col className="w-100 text-center text-white wow fadeInRight" data-wow-delay="0.2s" data-wow-duration="1.2s" style={{  cssText: 'font-family: eyesome; font-size: 3rem' }}>
                    Nicole
                </Col>                
            </Row>
            <Music/>
        </>
    )
}
export default Title;

